import React, { useContext, useEffect } from "react";
import { RouterProvider, createBrowserRouter, createHashRouter } from "react-router-dom";
import Layout from "./components/Layout/Layout";
import Home from "./components/Home/Home";
import Register from "./components/Register/Register";
import Products from "./components/Products/Products";
import Brands from "./components/brands/brands";
import Signin from "./components/Signin/Signin";
import Cart from "./components/cart/cart";
import Category from "./components/Category/Category";
import NotFound from "./components/notFound/notFound";
import UserContextProvider, { userContext } from "./context/userToken";
import ProtectedRouter from "./components/ProtectedRouter/ProtectedRouter";
import ForgotPassowrd from './components/forgotPassowrd/forgotPassowrd';
import ResetPassword from './components/resetPassword/resetPassword';
import toast, { Toaster } from 'react-hot-toast';
import Productsdetails from './components/productsdetails/productsdetails';
import Checkout from './components/checkout/checkout';
import Allorder from "./components/allorders/Allorder";
import { cartContext } from "./context/cartContext";

export default function App() {
  let {setToken}=useContext(userContext)
  let { getCartItem} = useContext(cartContext);
  const router = createHashRouter([
    {
      path: "",
      element: <Layout />,
      children: [
        { index: true, element: <ProtectedRouter><Home /></ProtectedRouter> },
        { path: "signup", element: <Register /> },
        { path: "signin", element: <Signin /> },

        { path: "products", element:<ProtectedRouter><Products /></ProtectedRouter>},
        { path: "brands", element:<ProtectedRouter><Brands /></ProtectedRouter>  },
        { path: "forgotpassword", element:<ForgotPassowrd/>},
        { path: "resetPassword", element:<ResetPassword/>},
        { path: "cart", element: <ProtectedRouter><Cart /></ProtectedRouter> },
        { path: "checkout", element: <ProtectedRouter><Checkout/></ProtectedRouter> },
        { path: "allorders", element: <ProtectedRouter><Allorder/></ProtectedRouter> },
        { path: "details/:id", element: <ProtectedRouter><Productsdetails/></ProtectedRouter> },
        { path: "category", element:<ProtectedRouter><Category /></ProtectedRouter>  },
        { path: "**", element: <NotFound /> },
      ],
    },
  ]);

  useEffect(async ()=>{
    if(localStorage.getItem('userToken') !==null){
    setToken(localStorage.getItem('userToken'))
    }
    await getCartItem()
    },[])
    
  return (
    <div>
    
        <RouterProvider router={router} />
        <Toaster />
    </div>
  );
}
