import React, { useEffect, useState } from 'react'
import { UseApi } from '../../hooks/useApi'
import { useOffline } from '../../hooks/useOffline'

export default function Brands() {
let x =useOffline()
let {dataList} =UseApi()
  return (
    <div>
    {x?
    <>
     {dataList.map((product)=>{
     return <div key={product._id}>
     <p>{product.title}</p>
  
     
     </div>
     })}
    </>:
    <p>this is offline</p>
}
    
    </div>
  )
}
