import axios from "axios";
import { useFormik } from "formik";
import React, { useContext } from "react";
import { cartContext } from "../../context/cartContext";

export default function Checkout() {
let {payment,getCartItem}=useContext(cartContext)

async function onlinePayment(values){
let {data} = await getCartItem()
let x =await payment(data.data._id,values)
// console.log(x.data.session.url)
window.location.href=x.data.session.url
// console.log(x)
}

  let formik = useFormik({
    initialValues: {
      details: "",
      phone: "",
      city: "",
    },
    onSubmit:onlinePayment
  });

  return (
    <div>
      <form onSubmit={formik.handleSubmit}>
        <label>Details:</label>
        <input onBlur={formik.handleBlur} type="text" id="details" value={formik.values.details} onChange={formik.handleChange} className="form-control" />
        <label>city:</label>
        <input onBlur={formik.handleBlur} type="text" id="city" value={formik.values.city} onChange={formik.handleChange} className="form-control" />
        <label>phone:</label>
        <input onBlur={formik.handleBlur} type="tel" id="phone" value={formik.values.phone} onChange={formik.handleChange} className="form-control" />
        <button type="submit" onClick={()=>onlinePayment()} className="btn bg-main text-light my-5">send</button>
      </form>
    </div>
  );
}
