import axios from "axios";
import { createContext } from "react";

export let cartContext = createContext();
let BaseUrl = `https://ecommerce.routemisr.com`;
let header = {
  token: localStorage.getItem("userToken"),
};
function AddToCart(id) {
  return axios.post(
    `${BaseUrl}/api/v1/cart`,
    {
      productId: id,
    },
    {
      headers: header,
    }
  );
}

function getCartItem() {
  return axios.get(
    `${BaseUrl}/api/v1/cart`,
    {
    
      headers: header,
    }
  ).then((resp)=>resp).catch((err)=>err)
}

function removeCartItem(id) {
  return axios.delete(`${BaseUrl}/api/v1/cart/${id}`, {
    headers: header,
  });
}

function update(id, count) {
  return axios.put(
    `${BaseUrl}/api/v1/cart/${id}`,
    {
      count: count,
    },
    {
      headers: header,
    }
  );
}
function payment(id,shippingAddress) {
  return axios.post(
    `https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}?url=https://www.routeegypt.com/`,
    {
        shippingAddress:shippingAddress
    },
    {
      headers: header,
    }
  );
}

export default function CartContextProvider(props) {
  return (
    <cartContext.Provider
      value={{ AddToCart, getCartItem, removeCartItem, update, payment }}
    >
      {props.children}
    </cartContext.Provider>
  );
}
