import React, { useContext, useEffect, useState } from "react";
import { cartContext } from "../../context/cartContext";
import { Link } from "react-router-dom";

export default function Cart() {
  const [productsItem, setProduct] = useState("");
  const [isError, setErr] = useState(null);
  const [productItem, setProducts] = useState([]);
  let { getCartItem ,removeCartItem,update} = useContext(cartContext);

  async function getItem() {
    let response = await getCartItem()
    if(response.data?.status == 'success'){
      setProduct(response.data?.data);
      setProducts(response.data?.data?.products);
    }else if (response.data?.statusMsg == 'fail'){
      setErr(response.response.data?.message)
    }
   

  }
  
  async function deleteItem(id){
  let {data} =await removeCartItem(id)
  setProducts(data.data.products)
  }
  
  async function updateItemCount(id,count,quantity){
  if(count < quantity){
    let {data}=await update(id,count)
    setProducts(data.data.products)
  }else{
  console.log('else')
  }
  }
  
  useEffect(() => {
    getItem();
  }, []);
  
  return (
  <>
    {!isError?
    <div className="container my-5">
    <div className="bg-main-light p-5">
      <h2>Shop Cart</h2>
      <h4 className="text-main">
        Total Price :{productsItem?.totalCartPrice}
      </h4>
      {productItem?.map((product) => {
        return (
        
          <div className="row  border-bottom py-5" key={product._id}>
            <div className="col-md-1">
              <img src={product.product.imageCover} className="w-100" />
            </div>
            <div className="col-md-11 d-flex justify-content-between align-items-center">
              <div>
                <h6 className="fw-bold">
                  {product.product.title.split(" ").slice(0, 2).join(" ")}
                </h6>
                <h6 className="text-main">{product.product.category.name}</h6>
                <p>price :{product.price}Egp</p>
                <button onClick={()=>{deleteItem(product.product._id)}} className="btn btn-outline-danger">
                  <i className="fa-regular fa-trash-can mx-2"></i>Remove
                </button>
              </div>
              <div>
              <button onClick={()=>{updateItemCount(product.product._id,product.count+1,product?.product.quantity)}} className="btn btn-outline-success">+</button>
              <span className="mx-2">{product.count}</span>
              <button onClick={()=>{updateItemCount(product.product._id,product.count-1,product?.product.quantity)}} className="btn btn-outline-success">-</button>
              </div>
            </div>
          </div>
        );
      })}
    </div>
    <button className="btn btn-success mx-auto my-5">
    <Link to='/checkout'>
    online Payment
    </Link>
    </button>
  </div>:
   <p>there is error</p>
  
  }
  
  </>

   
  );
}
