import { useEffect, useState } from "react"


export function useOffline(){

let [isOnline,setOnline]=useState(true)
function deceteOnline(){
    window.addEventListener('offline',()=>{
        console.log('offline')
        setOnline(false)
        })
        
        window.addEventListener('online',()=>{
        console.log('online')
        setOnline(true)
        })
}

useEffect(()=>{
deceteOnline()
},[])

return isOnline

}