import axios from 'axios'
import { useFormik } from 'formik'
import React from 'react'
import { useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
export default function ResetPassword() {
let navigate=useNavigate()
async function sendData(values){
let {data}=await axios.put(`https://ecommerce.routemisr.com/api/v1/auth/resetPassword`,values)
console.log(data)
if(data.token){
navigate('/signin')
}
}

let validationShema=Yup.object({
email:Yup.string().required('this email is required').email('this email is required'),
newPassword:Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{4,}$/i,'enter avalid password')
})

let formik=useFormik({
initialValues:{
  email:'',
  newPassword:''
},
validationSchema:validationShema
,
onSubmit:sendData
})

  return (
    <div>
    <form onSubmit={formik.handleSubmit} className='w-75 mx-auto my-5'>
    <label>email</label>
    <input onBlur={formik.handleBlur} onChange={formik.handleChange} id='email' value={formik.values.email} type='email' className='form-control'/>
    {formik.touched.email && formik.errors.email?<p className='text-danger'>{formik.errors.email}</p>:''}
    <label>new Passowrd</label>
    <input onBlur={formik.handleBlur} onChange={formik.handleChange} id='newPassword' value={formik.values.newPassword}  type='password' className='form-control'/>
    {formik.touched.newPassword && formik.errors.newPassword?<p className='text-danger'>{formik.errors.newPassword}</p>:''}
     
     <button className='btn bg-main text-light'>resetPassword</button>
    </form>
    </div>
  )
}
