import axios from 'axios'
import { useFormik } from 'formik'
import React,{useState} from 'react'
import { useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
export default function ForgotPassowrd() {
let navigate = useNavigate()
const[sucess,setMsg]=useState('')
const[err,setErr]=useState('')
let BaseUrl=`https://ecommerce.routemisr.com`;
async function sendData(values){
// console.log(formik)
let {data}=await axios.post(`${BaseUrl}/api/v1/auth/forgotPasswords`,values).catch((err)=>{
// console.log(err.response.data.message)
setErr(err.response.data.message)
});
if(data.statusMsg == 'success'){
setMsg(data.message)
document.querySelector('.verfiycode').classList.remove('d-none')
document.querySelector('.forgotpassword').classList.add('d-none')
setMsg('')
}
console.log(data)
}

let validationSchema=Yup.object({
email:Yup.string().required('this email is required').email('this is invalid email')
})
let formik = useFormik({
initialValues:{
email:''
},
validationSchema:validationSchema
,
onSubmit:sendData
})
async function sendCode(values){
  let {data}=await axios.post(`${BaseUrl}/api/v1/auth/verifyResetCode`,values).catch((err)=>{
    console.log(err.response.data.message)
    setErr(err.response.data.message)
})
if(data.status == 'Success'){
  navigate('/resetPassword')
}
}

let validationSchema2=Yup.object({
  resetCode:Yup.string().required('this email is required')
  })
  let verfiyForm = useFormik({
  initialValues:{
  resetCode:''
  },
  validationSchema:validationSchema2
  ,
  onSubmit:sendCode
  })

  return (
  <>
    <div className='forgotpassword'>
    <form onSubmit={formik.handleSubmit} className='w-75 mx-auto my-5'>
   {err? <p className='text-danger'>{err}</p>: <p className='text-success'>{sucess}</p>}

     <label>email</label>
     <input onBlur={formik.handleBlur} onChange={formik.handleChange} id='email' name='email' value={formik.values.email}   className='form-control' type='email'/>
     {formik.touched.email && formik.errors.email ? <p className='text-danger'>{formik.errors.email}</p>:'' }
     <button disabled={!(formik.isValid && formik.dirty)} className='btn bg-main text-light my-3'>send Code</button>
    </form>
    </div>
    
    <div className='verfiycode d-none'>
    <form onSubmit={verfiyForm.handleSubmit} className='w-75 mx-auto my-5'>
   {err? <p className='text-danger'>{err}</p>: <p className='text-success'>{sucess}</p>}

     <label>ResetCode:</label>
     <input onBlur={verfiyForm.handleBlur} onChange={verfiyForm.handleChange} id='resetCode' name='resetCode' value={verfiyForm.values.resetCode}   className='form-control' type='text'/>
     {verfiyForm.touched.resetCode && verfiyForm.errors.resetCode ? <p className='text-danger'>{verfiyForm.errors.resetCode}</p>:'' }
     <button disabled={!(verfiyForm.isValid && verfiyForm.dirty)} className='btn bg-main text-light my-3'>verifyCode</button>
    </form>
    </div>
  </>
  
  )
}
